/* SubmissionSuccess.module.css */

.container {
    width: 375px; /* Fixed width */
    height: 812px; /* Fixed height */
    border-radius: 40px;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
    overflow-y: auto; 
    overflow-x: hidden; 
}

.header {
    width: 100%;
    background-color: #4a90e2; /* Header background color */
    padding: 15px;
    color: white;
    text-align: center;
}

.scrollableContent {
    flex: 1; /* Allow this area to grow and take up remaining space */
    width: 100%;
    padding: 20px;
    background-color: white; /* White background for content */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    border-radius: 8px; /* Rounded corners */
    overflow-y: auto; /* Allow scrolling if content overflows */
}

.title {
    font-size: 24px; /* Title font size */
    margin-bottom: 20px;
    color: #333; /* Dark color for the title */
}

h2 {
    font-size: 20px; /* Subtitle font size */
    margin-top: 20px;
    color: #4a90e2; /* Consistent color for subtitles */
}

ul {
    list-style-type: none; /* Remove default bullet points */
    padding: 0;
    margin: 10px 0;
}

li {
    padding: 8px 0; 
    font-size: 16px; /* Font size for list items */
    color: #555; /* Gray color for text */
}

.declaration {
    margin-top: 20px;
    display: flex;
    align-items: center; 
}

.declaration input {
    margin-right: 10px; 
}

.submitButton {
    margin-top: 20px;
    padding: 10px 20px; 
    background-color: #4a90e2; /* Button background color */
    color: white; /* Button text color */
    border: none; /* Remove border */
    border-radius: 5px; /* Rounded corners for button */
    cursor: pointer; /* Cursor indicates it's clickable */
}

/* Add hover effect for the button */
.submitButton:hover {
    background-color: #357ABD; /* Darker shade on hover */
}

.footer {
    width: 100%;
    background-color: #4a90e2; /* Footer background color */
    padding: 15px;
    color: white;
    text-align: center;
    position: absolute; /* Fixed position */
    bottom: 0; /* Stick to bottom */
}
