.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); 
    border: 10px solid black;
    /* Add relative positioning */
    position: relative;
}

.container::before {
    content: "";
    position: absolute;
    top: 10px; 
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: rgb(0, 0, 0); 
    border-radius: 50%;
    box-shadow: 0 0 0 1px black; 
    z-index: 2;
}

.header {
    background-color: #f57c00;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    color: white;
    text-align: center;
    position: relative; 
    z-index: 1;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
}

.logo {
    height: 50px;
}

.title {
    font-size: 16px;
    font-weight: bold;
    margin-top: 20px;
}

.content {
    flex: 1;
    padding: 20px;
    text-align: center;
}

.contentTitle {
    font-size: 24px;
    color: #f57c00;
    margin-bottom: 10px;
}

.description {
    font-size: 14px;
    color: #666;
    margin-bottom: 20px;
}

.waterBillForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin-bottom: 30px;
}

.waterBillForm label {
    font-size: 14px;
    color: #333;
}

.waterBillForm input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 200px;
}

.submitButton {
    padding: 10px 20px;
    background-color: #f57c00;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.backButton {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.navbar {
    background-color: #f57c00;
    display: flex;
    justify-content: space-around;
    padding: 15px 0;
    border-bottom-left-radius: 22px;
    border-bottom-right-radius: 22px;
}

.navItem {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navIcon {
    width: 24px;
    height: 24px;
}

.navItem p {
    margin-top: 5px;
    font-size: 12px;
    color: white;
}
.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; 
}

.modalContent {
    background: white;
    padding: 20px;
    border-radius: 10px;
    max-width: 300px;
    width: 90%;
    text-align: center;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); 
    transition: transform 0.3s ease; 
    transform: translateY(-10px); 
}

.modalContent h3 {
    margin-bottom: 15px;
    font-size: 24px;
    color: #333; 
}

.modalContent p {
    margin: 12px 0;
    font-size: 16px;
    color: #555; 
}

.closeButton,
.payNowButton {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 15px;
    width: 100%; 
    font-size: 16px;
    transition: background-color 0.3s ease; 
}

.closeButton:hover,
.payNowButton:hover {
    background-color: #0056b3; 
}

.payNowButton {
    background-color: #28a745; 
}

.payNowButton:hover {
    background-color: #218838; 
}


.modalContent > div {
    margin-bottom: 10px; 
}

.paymentOption {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    border: 1px solid lightgray;
}

.paymentIcon {
    width: 50px; 
    height: auto; 
    margin-right: 10px;
}

