body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0; 
}

.mobilecontainer {
  width: 375px;
  height: 812px;
  border-radius: 40px;
  overflow: hidden;
  background-color: white;
  display: flex;
  flex-direction: column;
  position: relative;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); 
  border: 10px solid black; 
}

.mobilecontainer:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  pointer-events: none;
  z-index: 10; 
  border-radius: 30px; 
}

.topsection {
  background-color: #f57c00;
  height: 380px;
  position: relative;
  text-align: center;
  z-index: 1;
  display: grid;
}

.subtop {
  background-color: #fffdfd;
  border-radius: 20px;
  height: 90px;
}

.usalogo {
  position: absolute;
  top: 10px;
  left: 20px;
  width: 80px;
  height: auto;
  z-index: 2;
}

.orissalogo {
  position: absolute;
  top: 10px;
  right: 20px;
  width: 60px; 
  height: auto;
  border-radius: 5px;
  z-index: 2; 
}

.odishamap {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  width: 160px;
  height: auto;
  z-index: 1;
}

.odishaText {
  position: absolute;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 20px; 
  color: rgb(0, 0, 0); 
  text-align: center;
  z-index: 2; 
}

.cm {
  position: absolute;
  top: 75px;
  right: 40%;
  width: 300px;
  height: auto;
  z-index: 2;
}

.swami {
  position: absolute;
  top: 135px;
  left: 60%;
  width: 200px;
  height: auto;
  z-index: 2;
}

.loginsection {
  padding: 20px;
  background-color: white;
  text-align: center;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
}

.loginsection h2 {
  color: #f57c00;
  margin-bottom: 20px;
  font-size: 24px;
}

.loginform {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

.loginform input {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  width: 90%;
}

.loginbtn {
  padding: 12px;
  background-color: #f57c00;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s; 
  ;
}

.loginbtn:hover {
  background-color: #ff8c00;
}

.forgotpassword {
  text-decoration: none;
  color: #f57c00;
  font-size: 14px;
}

.remembersection {
  display: flex;
  align-items: center; 
  justify-content: flex-start;
  margin-top: 10px;
  white-space: nowrap; 
}

.checkbox {
  margin-left: auto; 
}

.googlebtn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  margin: 5px 0;
  width: 90%;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: #fff;
}

.googleImage {
  width: 20px; 
  height: auto;
  margin-right: 8px; 
}

.sociallogin {
  margin-top: 10px;
}

.sociallogin p {
  margin-bottom: 10px;
  font-size: 14px;
}

.footer {
  background-color: #f57c00;
  padding: 20px;
  text-align: center;
}

.helpdesk p {
  color: white;
  margin-bottom: 10px;
  font-size: 16px;
}

.helpdesk a {
  color: white;
  display: block;
  text-decoration: none;
  font-size: 14px;
  margin-bottom: 5px;
}

.mobilecontainer:before {
  content: '';
  position: absolute;
  top: 0px; 
  left: 50%;
  width: 30px; 
  height: 30px; 
  background-color: #000000; 
  border-radius: 50%; 
  transform: translateX(-50%);
  z-index: 5; 
}

.warning {
  color: red;                      
  font-size: 14px;                  
  margin-top: 10px;                 
  font-weight: bold; /* Make the warning text bold */
  text-align: center; /* Center align the warning message */
}

.signUpPrompt {
  margin-top: 15px;                
  text-align: center;               
}

.signUpLink {
  color: #f57c00;                     
  text-decoration: underline;      
  cursor: pointer; /* Add pointer cursor for better UX */
}

.signUpLink:hover {
  text-decoration: none; /* Remove underline on hover for effect */
  font-weight: bold; /* Make the link bold on hover */
  color: #e57c00; /* Darker shade on hover */
}

/* Sign Up Button */
.signupbtn {
  padding: 12px;
  background-color: #f57c00;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s; /* Add transition for transform */
}

.signupbtn:hover {
  background-color: #ff8c00;
  transform: scale(1.05); /* Slightly enlarge button on hover */
}
