@font-face {
    font-family: 'Stencil Std Bold';
    src: url('./Stencil Std Bold.ttf') format('truetype');
}

.header {
    background-color: white;
    display: flex;
    justify-content: space-between; 
    align-items: center;
    padding: 12px 24px; 
    color: rgb(0, 0, 0);
    text-align: center;
    position: relative;
    z-index: 1;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
    border-bottom: 1px solid lightgray;
}

.logo {
    height: 60px;
    width: auto;
    /* border: 1px solid red; */
    /* margin-right: 10px; */
}
.logo1 {
    height: 70px;
    width: auto;
    /* border: 1px solid red; */
    margin-left: 8px;
}
.titleContainer {
    flex-grow: 1;
    text-align: center;
    margin: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.titleGroup {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    /* Removes extra spacing */
}

.title {
    font-size: 42px; 
    font-weight: bolder;
    font-family: 'Stencil Std Bold', sans-serif; 
    letter-spacing: 2px;
    color: #8B4513;
    margin: 0;
    padding: 0;
    line-height: 1; /* Controls the space between lines */
}

.subtitle {
    font-size: 10px;
    font-weight: bold;
    color: #8B4513;
    white-space: pre-wrap;
    text-align: center;
    margin: 0;
    padding: 0;
    line-height: 1.2; /* Controls space between title and subtitle */
}

.caption {
    font-size: 10px;
    margin-top: 3px;
    color: gray;
    font-family: 'Times New Roman', Times, serif;
    text-align: center;
}

@media (max-width: 600px) {
    .header {
        flex-direction: column; 
        align-items: center;
    }

    .titleContainer {
        margin: 10px 0;
    }

    .title {
        font-size: 20px;
    }

    .subtitle {
        font-size: 14px;
        white-space: normal;
    }

    .caption {
        font-size: 10px;
    }
}
