.navbar {
    background-color: #f57c00;
    display: flex;
    justify-content: space-around;
    padding: 15px 0;
    border-bottom-left-radius: 22px;
    border-bottom-right-radius: 22px;
}

.navItem {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navIcon {
    width: 24px;
    height: 24px;
}

.navItem p {
    margin-top: 5px;
    font-size: 12px;
    color: white;
}
