body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
    background-color: #f5f5f5; 
}

.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); 
    border: 10px solid black; 
}


.container::before {
    content: "";
    position: absolute;
    top: 10px; 
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: rgb(0, 0, 0); 
    border-radius: 50%;
    box-shadow: 0 0 0 1px black; 
    z-index: 2;
}


.header {
    background-color: #f57c00;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    color: white;
    text-align: center;
    position: relative; 
    z-index: 1;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
}

.logo {
    height: 50px;
}

.title {
    font-size: 16px;
    font-weight: bold;
    margin-top: 20px;
}

.grid {
    display: flex;
    flex-direction: column; 
    flex-grow: 1;  
    padding: 0 10px;
    padding-bottom: 60px; 
}

.suppliers{
    
    height: 600px;
}

.suppliersTitle {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin-top: 40px;
    margin-bottom: 20px;
}

.supplierItem {
    border: 1px solid #ccc; 
    border-radius: 8px; 
    padding: 10px; 
    display: flex; 
    align-items: center; 
    justify-content: flex-start; 
    text-align: left;
    background-color: #f9f9f9; 
    transition: transform 0.2s;
    margin: 5px 0; 
    width: calc(100% - 20px);
}

.supplierLogo {
    width: 35px; 
    height: 35px; 
    margin-right: 10px; 
}




.backButton {
    background-color: transparent; 
    text-align: left;
    margin-top: 10px;
    border: none;
    color: rgb(0, 0, 0); 
    font-size: 16px;
    cursor: pointer;
    margin-right: 10px; 
}

.searchInput {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: calc(100% - 40px); 
    margin: 10px 10px; 
    font-size: 16px; 
}

.backButton {
    background-color: transparent; 
    border: none; 
    cursor: pointer;
    padding: 10px; 
}

.backIcon {
    width: 24px; 
    height: 24px; 
}
