.container {
    width: 375px;
    height: 812px; 
    border-radius: 40px;
    overflow: hidden; 
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
}

.header {
    position: sticky;
    top: 0; 
    background-color: white; 
    z-index: 10; 
}

.footer {
    position: sticky;
    bottom: 0; 
    background-color: white; 
    z-index: 10; 
}

.scrollableContent {
    overflow-y: auto; 
    flex: 1; 
    padding: 20px;
}


.scrollableContent::-webkit-scrollbar {
    width: 8px;
}

.scrollableContent::-webkit-scrollbar-thumb {
    background-color: lightgray;
    border-radius: 10px; 
}

.scrollableContent::-webkit-scrollbar-track {
    background-color: #f0f0f0; 
}

.scrollableContent {
    scrollbar-width: thin;
    scrollbar-color: #f8f8f8 #f0f0f0; 
}

.title {
    font-size: 22px; 
    margin-bottom: 15px; 
    text-align: center; 
}

.formGroup {
    margin-bottom: 10px; 
}

.inputField {
    width: 100%;
    padding: 10px; 
    border: 1px solid lightgray;
    border-radius: 4px;
    font-size: 16px; 
    margin-bottom: 6px;
    
}

.buttonGroup {
    display: flex;
    justify-content: space-between;
    margin-top: 35px; 
   
}

.prevButton, .nextButton {
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px; 
}

.prevButton {
    background-color: #f0f0f0;
}

.nextButton {
    background-color: #007bff;
    color: white;
}

.required {
    color: red; 
}

.subtitle {
    font-size: 20px;
    margin: 15px 0;
}

.genderOptions {
    display: flex;
    justify-content: space-around; 
    margin-bottom: 10px;
}

.disabled {
    background-color: #ccc; 
    cursor: not-allowed; 
}
