.stepIndicator {
    display: flex;
    justify-content: space-between;
    margin: 20px 0; /* Top and bottom margin */
}

.stepContainer {
    display: flex;
    flex-direction: column; /* Stack number above text */
    align-items: center; /* Center align the text */
    flex: 1; /* Make each step occupy equal space */
    position: relative; /* For positioning tick marks */
}

.step,
.activeStep {
    padding: 10px;
    border-radius: 50%; /* Circular shape */
    background-color: #f0f0f0; /* Default background color */
    color: #333; /* Default text color */
    cursor: pointer;
    transition: background-color 0.3s; /* Smooth color transition */
    width: 40px; /* Fixed width for the circle */
    height: 40px; /* Fixed height for the circle */
    display: flex; /* Flex container */
    justify-content: center; /* Center the number */
    align-items: center; /* Center the number */
}

.activeStep {
    background-color: #007bff; /* Active step color */
    color: #fff; /* Text color for active step */
}

.completedStep {
    background-color: #4caf50; /* Green background for completed step */
    color: white; /* White tick mark color */
    border-radius: 50%; /* Circular shape */
    width: 40px; /* Match size of the step circle */
    height: 40px; /* Match size of the step circle */
    display: flex; /* Flex container */
    justify-content: center; /* Center the tick mark */
    align-items: center; /* Center the tick mark */
    font-size: 24px; /* Adjust size for tick */
    position: relative; /* Ensure it stays in place */
}

.stepText {
    margin-top: 8px; /* Space between the number and the text */
    color: #333; /* Default text color */
    font-weight: 500; /* Slightly bold for contrast */
}

/* Hover effect for the default step */
.step:hover {
    background-color: #007bff; /* Change color on hover */
    color: #fff; /* Text color on hover */
}
