.profileContainer {
    width: 375px; 
    height: 812px; 
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); 
    border: 10px solid black; 
}


.profileContainer::before {
    content: "";
    position: absolute;
    top: 10px; 
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: rgb(0, 0, 0); 
    border-radius: 50%;
    box-shadow: 0 0 0 1px black; 
    z-index: 2;
}


.backButton {
    position: absolute;
    top: 15px;
    left: 15px;
    padding: 5px 15px;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 10px;
    cursor: pointer;
    font-size: 14px;
}

.profileContent {
    margin-top: 50px;
    flex-grow: 1;
    text-align: center;
    padding: 20px;
}

.profileImageContainer {
    margin: 20px 0;
}

.profileImage {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid #f57c00;
}

.profileDetails {
    margin-top: 20px;
    font-size: 16px;
}

.profileDetails h2 {
    margin-bottom: 10px;
    font-size: 20px;
}

.profileDetails p {
    margin: 14px 0;
    text-align: left ;
    margin-left: 20px;
}

.logoutButton {
    margin-top: 50px;
    padding: 10px 20px;
    background-color: #f57c00;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    width: 150px;
}

.logoutButton:hover {
    background-color: #ff9f40;
}
