.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    overflow-y: hidden; /* Ensure horizontal scrolling is disabled */
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
}

.formContainer {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    overflow-y: auto; /* Only enable vertical scrolling */
    width: 100%; /* Ensure full width usage without causing horizontal scroll */
    box-sizing: border-box; /* Prevent padding from adding to width */
}

.title {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 20px;
}

.stepIndicator {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.step {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #a32b2b;
    margin: 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.activeStep {
    background-color: #a3a09f;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.formGroup {
    margin-bottom: 15px;
    width: 100%;
}

.formGroup label {
    font-size: 14px;
    color: #333;
    display: block;
    margin-bottom: 5px;
    text-align: left;
}

.inputField {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    box-sizing: border-box; 
}

.nextButton {
    width: 100%;
    padding: 15px;
    background-color: #ff5722;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    margin-top: 20px;
    cursor: pointer;
}

.nextButton:hover {
    background-color: #e64a19;
}

.required {
    color: red;
    margin-left: 5px;
}
