.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); 
    border: 10px solid black; 
}

