.header, .footer {
    flex-shrink: 0;
    background-color: #f57c00;
    color: white;
    text-align: center;
    padding: 10px;
}

.content {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    max-height: calc(100% - 80px);
    &::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.bmcSection {
    text-align: center;
    margin-top: 10px;
}

.bmcLogo {
    width: 360px;
    height: auto;
}

.welcomeText {
    font-size: 14px;
    font-weight: bold;
    color: #f57c00;
    margin-bottom: 15px; 
}

.categoryTitle {
    font-size: 14px; 
    font-weight: bold;
    color: black;
    margin-top: 15px; 
    margin-bottom: 5px;
    margin-left: 5px;
    text-align: left;
    padding: 4px; 
    border-radius: 6px;
    /* background-color: #f0f0f0; */
}

.titleRow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    

}

.viewAll {
    color: #f57c00;
    font-weight: bold;
    cursor: pointer;
    font-size: 12px;
    margin-right: 5px;
    border: 1px solid lightgray;
    padding: 5px;
    border-radius: 6px;
}

.divider {
    width: 100%;
    height: 1px;
    background-color: #ddd;
    margin: 10px 0;
}

.scrollButton {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #f57c00;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 5px;
    font-size: 14px;
    cursor: pointer;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s ease;
}

.scrollButton:hover {
    background-color: #ff8c00;
}

.grid1 {
    display: grid;
    grid-template-columns: repeat(4, 1fr); 
    gap: 10px; 
    margin: 10px; 
    justify-items: center;
    align-items: center;
}




