.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
    overflow-y: auto; 
    overflow-x: hidden; 
}

.formContainer {
    width: 90%;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow-y: auto; 
    overflow-x: hidden;
    max-height: 100%; 
}

.formContainer::-webkit-scrollbar {
    width: 2px; 
}

.formContainer::-webkit-scrollbar-thumb {
    background-color: #8f4d4d; 
    border-radius: 10px;
}

.formContainer::-webkit-scrollbar-thumb:hover {
    background-color: #555; 
}

.formContainer::-webkit-scrollbar-track {
    background-color: #f1f1f1; 
}

.title {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
}

.formGroup {
    margin-bottom: 20px;
}

.formGroup label {
    display: block;
    margin-bottom: 8px;
    font-size: 1rem;
    font-weight: 500;
    color: #333;
}
.stepIndicator {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

.step {
    padding: 10px 20px;
    font-size: 1.2rem;
    font-weight: 500;
    color: #ccc;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.activeStep {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
}
.required {
    color: red;
}

.inputField {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #fff;
    transition: border 0.3s ease;
}

.inputField:focus {
    border-color: #007bff;
    outline: none;
}

.genderOptions {
    display: flex;
    gap: 15px;
}

.genderOptions label {
    font-size: 1rem;
    color: #333;
}

textarea {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    min-height: 80px;
    resize: vertical;
}

.buttonGroup {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.prevButton,
.nextButton {
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.prevButton:hover,
.nextButton:hover {
    background-color: #0056b3;
}

@media (max-width: 768px) {
    .formContainer {
        width: 90%;
    }

    .title {
        font-size: 1.8rem;
    }

    .prevButton,
    .nextButton {
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .formContainer {
        width: 100%;
        padding: 15px;
    }

    .title {
        font-size: 1.5rem;
    }

    .prevButton,
    .nextButton {
        font-size: 0.8rem;
        padding: 8px 16px;
    }
}
