
.container {
    width: 375px;
    height: 812px; 
    border-radius: 40px;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
    overflow: hidden; 
}

.header {
    width: 100%;
    background-color: #4a90e2; 
    padding: 15px;
    color: white;
    text-align: center;
    flex-shrink: 0; 
}

.scrollableContent {
    flex: 1; 
    width: 90%;
    padding: 20px;
    background-color: white; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    border-radius: 8px; 
    text-align: center; 
    overflow-y: auto;
}

h1 {
    font-size: 18px; 
    margin-bottom: 10px;
    color: #333; 
}

p {
    font-size: 16px; 
    color: #555;
    margin: 5px 0; 
    margin-top: 20px;
}

.footer {
    width: 100%;
    background-color: #4a90e2;
    padding: 15px;
    color: white;
    text-align: center;
    flex-shrink: 0; 
}
