.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
}

.content {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    overflow-y: auto;
}

.pageTitle {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 20px;
}

.options {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.option {
    width: 90%;
    padding: 25px;
    margin: 10px 0;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 10px;
    font-size: 16px;
    color: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s ease;
}

.option:hover {
    background-color: #f5f5f5;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.extraLinks {
    margin-top: 30px;
    font-size: 16px;
}

.extraLinks p {
    color: #007BFF;
    cursor: pointer;
    margin: 10px 0;
    text-decoration: underline;
}

.extraLinks p:hover {
    color: #0056b3;
}
