.container {
    width: 375px; 
    height: 812px; 
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); 
    border: 10px solid black; 
}

.container::before {
    content: "";
    position: absolute;
    top: 10px; 
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: rgb(0, 0, 0); 
    border-radius: 50%;
    box-shadow: 0 0 0 1px black; 
    z-index: 2;
}

.header {
    padding: 20px; /* Add padding for better spacing */
    text-align: center; /* Center the header text */
    font-size: 24px; /* Adjust font size */
}

.form {
    display: flex;
    flex-direction: column;
    gap: 10px; /* Reduced gap for smaller devices */
    padding: 20px; /* Padding for form */
    flex: 1; /* Allow form to grow within the container */
    overflow-y: auto; /* Enable scrolling if content overflows */
}

.formGroup {
    display: flex;
    flex-direction: column;
}

label {
    font-size: 14px;
    margin-bottom: 5px;
}

input[type="text"] {
    padding: 10px; 
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 14px;
}

.submitButton {
    padding: 10px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px; 
}

.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; 
}

.popupContent {
    background: white;
    padding: 20px;
    border-radius: 8px;
    width: 90%;
    max-width: 330px; 
    /* max-height: auto;  */
    overflow-y: auto; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
 
}

.popupContent h3 {
    margin-top: 0;
}

.adultCount {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0;
}

.adultSelector {
    display: flex;
    align-items: center;
}

.adultSelector button {
    padding: 5px 10px;
    margin: 0 5px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.adultSelector button:hover {
    background-color: #45a049;
}

.confirmButton {
    width: 100%;
    padding: 10px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 15px;
}

.confirmButton:hover {
    background-color: #0056b3;
}

.closeButton {
    margin-top: 10px;
    background-color: red;
    color: white;
    border: 1px solid lightgray;
    padding: 6px 10px;
    border-radius: 5px;
   
    
}

.closeButton:hover {
    background-color: #c82333;
}

.popupContent p {
    margin: 10px 0;
    font-size: 14px;
    color: #555;
    text-align: left; 
}

.popupContent p strong {
    color: #333;
}

.popupContent .note {
    font-style: italic;
    color: #888;
}
.backButton {
    background-color: #171717;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 6px 10px;
    cursor: pointer;
    margin-top: 10px; 
    display: flex;
    text-align: left;
}

.backButton:hover {
    background-color: #090909;
}
