.container {
    width: 375px;
    height: 812px;
    border-radius: 40px;
    overflow: hidden;
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border: 10px solid black;
    justify-content: space-between; 
}

.container::before {
    content: "";
    position: absolute;
    top: 10px; 
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: rgb(0, 0, 0); 
    border-radius: 50%;
    box-shadow: 0 0 0 1px black; 
    z-index: 2;
}

.passDetailSection {
    padding: 20px;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    overflow-y: auto; 
    overflow-x: hidden; 
}

.passDetailSection::-webkit-scrollbar {
    width: 8px;
}

.passDetailSection::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 4px; 
}

.passDetailSection::-webkit-scrollbar-track {
    background-color: #f1f1f1; 
}

.backButton {
    background-color: #4CAF50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    margin-bottom: 15px;
    transition: background-color 0.3s ease;
    align-self: flex-start;
}

.backButton:hover {
    background-color: #45a049;
}

.heading {
    font-size: 20px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
}

.passInfo {
    background-color: #f5f5f5;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 20px;
    width: 100%;
    text-align: left;
}

.passInfo h3 {
    font-size: 18px;
    margin-bottom: 5px;
    color: #333;
}

.passInfo p {
    font-size: 14px;
    color: #777;
    margin: 5px 0;
}

.passengerDetails {
    background-color: #f9f9f9;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 20px;
    width: 100%;
    text-align: left;
}

.fareSplit {
    background-color: #fff0e6;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 20px;
    width: 100%;
    text-align: left;
}

.terms {
    background-color: #fff7e6;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 20px;
    width: 100%;
    text-align: left;
}

.terms a {
    color: #007BFF;
    text-decoration: underline;
}

.paymentSection {
    padding: 15px;
    background-color: #e6f7ff;
    border-radius: 8px;
    width: 100%;
    text-align: center;
}

.paymentSection h4 {
    font-size: 18px;
    margin-bottom: 10px;
    color: #333;
}

.paymentSection p {
    font-size: 16px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
}

.paymentButton {
    background-color: #007BFF;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%;
}

.paymentButton:hover {
    background-color: #0056b3;
}


.footer {
    padding: 15px;
    background-color: #f5f5f5;
    text-align: center;
}


.paymentModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContent {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    width: 300px;
}

.paymentOptions {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.paymentIcon {
    width: 50px;
    height: 50px;
    cursor: pointer;
}

.processingOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.processingBar {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
}

.progressBar {
    width: 100%;
    background-color: #e0e0e0;
    border-radius: 5px;
    height: 10px;
    margin-top: 10px;
}

.progress {
    height: 10px;
    background-color: #4caf50;
    width: 0;
    animation: progressAnimation 3s linear forwards;
}

@keyframes progressAnimation {
    0% { width: 0; }
    100% { width: 100%; }
}

